<template>
  <div>
    <section class="user_work_range">
      <div class="user_work_item" v-for="(workInfo, index) in myWorkList" @touchend="dropDown" :key="index">
        <div class="user_item_header">
          <div>
            <img :src="workInfo.userAvatar" :alt="workInfo.nickname" :title="workInfo.nickname">
          </div>
          <div>
            <p>{{workInfo.nickName}}</p>
            <p>{{new Date(workInfo.dateAdded) | moment('YYYY-MM-DD HH:mm:ss')}}</p>
          </div>
        </div>
        <div class="user_item_content" @click="enterPerCenter(workInfo)">
          <p>{{workInfo.content}}</p>
          <div class="overflow_content">
            <img v-for="(workImgInfo, index) in workInfo.contentPics.split(',')" :src="workImgInfo" alt="作品" :key="index">
            <div v-if="workInfo.contentPics.indexOf('http') != -1 && workInfo.contentPics.split(',').length > 6" class="workWask">{{workInfo.contentPics.split(',').length}}+</div>
          </div>
        </div>
        <div class="user_mutual">
          <div @click="activePraise(workInfo)"><img :src="workInfo.isPraise == 'Y' ? yesPraiseImg :　noPraiseImg" alt="点赞"><span>{{workInfo.numberFabulous}}</span></div>
          <div @click="enterPerCenter(workInfo)"><img :src="commentImg" alt="评论"><span>{{workInfo.numberComments}}</span></div>
          <div><img :src="shareImg" alt="分享"><span>{{workInfo.numberShare}}</span></div>
        </div>
      </div>
    </section>
    <div class="seven_day_info" v-if="totalWork == myWorkList.length && totalWork !== 0">只展示近7天的信息</div>
    <div v-if="totalWork == 0 || !myWorkList" class="no-data-img">
      <img :src="noDatalist" alt="没有作品哦~">
      <span>暂时没有内容,宝宝先看看别的吧~</span>
    </div>
  </div>
</template>
<script>
  export default {
    components: {

    },
    data() {
      return {
        page: 1,
        myWorkList: new Array(),
        baseUrl: 'http://192.168.124.4:8081',
        noPraiseImg: require('../../assets/img/jef4eada.png'),
        yesPraiseImg: require('../../assets/img/jef4f7vr.png'),
        commentImg: require('../../assets/img/jehyxybc.png'),
        shareImg: require('../../assets/img/jehyyijh.png'),
        noDatalist: require('../../assets/img/da.png'),
        falgNoData: false,
        pagination: true,
        showData: false,
        totalWork: 0
      }
    },
    created() {

      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })

      this.axios.get('/api/myWorkCircleCenter', {
        params: {
          userId: this.$route.params.id,
          page: this.page,
          workState: 'PASSED'
        }
      }).then(data => {
        this.myWorkList = data.data.result.list
        this.myWorkList.forEach(element => {
          if (element.userAvatar.indexOf('http') == -1) {
            element.userAvatar = this.baseUrl + element.userAvatar
          }
        })
        this.$Indicator.close()
      }).catch(err => {
        console.log(err);
      });
    },
    methods: {
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return;
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1
          this.axios.get('/api/myWorkCircleCenter', {
            params: {
              userId: this.$route.params.id,
              page: this.page,
              workState: 'PASSED'
            }
          }).then((res) => {
            if (this.pagination) {
              this.totalWork = res.data.result.total
              if (res.data.result.list.length != 0) {
                this.myWorkList = this.myWorkList.concat(res.data.result.list);
                this.$Indicator.close()
              } else {
                this.$Indicator.close()
                this.$Toast({
                  message: '没有更多作品了',
                  position: 'middle',
                  duration: 1000
                })
                this.pagination = false
                this.showData = true
              }
            }
          }).catch((err) => {
            console.log(err);
          });
        }
      },
      // 进入作品详情
      enterPerCenter(info) {
        this.axios.get('/api/plusWorks', {
          params: {
            worksId: info.worksId,
            type: 'LL'
          }
        }).then(data => {
          console.log(data);
          this.$router.push({
            name: 'circleDetail',
            params: {
              id: info.worksId
            }
          });
        }).catch(err => {
          console.log(err)
        })
      },
      // 点赞
      activePraise(workInfo) {
        this.axios.get('/api/likeWorks', {
          params: {
            worksId: workInfo.worksId,
            action: workInfo.isPraise == 'Y' ? 'NORMAL' : 'UPVOTE'
          }
        }).then(data => {
          if (data.data.status === 'success' && data.data.result == '取消点赞 -1！') {
            workInfo.isPraise = 'N'
            workInfo.numberFabulous = --workInfo.numberFabulous
            this.$Toast({
              message: '取消点赞成功',
              position: 'middle',
              duration: 1000
            })
          } else if (data.data.status === 'success' && data.data.result == '点赞 +1！') {
            workInfo.isPraise = 'Y'
            workInfo.numberFabulous = ++workInfo.numberFabulous
            this.$Toast({
              message: '点赞成功',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          console.log(err)
        });
      },
    }
  }
</script>
<style lang="less" scoped>
  /* 用户作品圈信息 START */
  .no-data-img{
    text-align: center;
    transform: scale(0.6);
    &>span{
      display: block;
      color: #777;
      font-size: 40px;
    }
  }
  .user_work_range {
    height: 100%;
    width: 100%;
  }
  .user_work_item {
    margin-top: .625rem;
    background-color: #fff;
  }
  .user_item_header {
    display: flex;
  }
  .user_item_header {
    &>div:nth-child(1) {
      text-align: center;
      flex: 2;
      &>img {
        border-radius: 50%;
        width: 2.5rem;
        height: 2.5rem;
      }
    }
  }
  .user_item_header>div:nth-child(2) {
    color: #555;
    flex: 8;
  }
  .user_item_content {
    width: 100%;
    padding: .625rem 0 0 .9375rem;
    box-sizing: border-box;
    position: relative;
    &>p {
      font-size: 1.125rem;
      height: 2.5rem;
      box-sizing: border-box;
    }
    .overflow_content {
      width: 100%;
      height: auto;
      max-height: 12.5rem;
      overflow: hidden;
      position: relative;
      &>.workWask {
        position: absolute;
        bottom: 0;
        right: 0;
        height: 1.5625rem;
        width: 3.125rem;
        background-color: rgba(0, 0, 0, .3);
        z-index: 1;
        color: #fff;
        text-align: center;
        line-height: 1.5625rem;
        border-radius: 1.25rem;
      }
      &>img {
        width: 33%;
        height: 6.25rem;
      }
    }
  } // 点赞/评论/分享
  .user_mutual {
    display: -webkit-flex;
    display: flex;
    height: 2.5rem;
    &>div {
      -webkit-box-flex: 1;
      flex: 1;
      text-align: center;
      height: 2.5rem;
      line-height: 2.5rem;
      &>img {
        width: 1.25rem;
        height: 1.25rem;
        vertical-align: middle;
      }
      &>span {
        vertical-align: middle;
        margin-left: .625rem;
      }
    }
  }
  .seven_day_info {
    width: 100%;
    text-align: center;
    padding: 1.25rem 0;
  }
  /* 用户作品圈信息 END */
</style>




